/* переменные и миксины */
/* main variables */
* { box-sizing: border-box; outline: none !important; }

/* reset */
html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5, h6, p, blockquote, pre, a, abbr, acronym, address, big, cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small, strike, strong, sub, sup, tt, var, b, u, i, center, fieldset, form, label, legend, table, caption, tbody, tfoot, thead, tr, th, td, article, aside, canvas, details, embed, figure, figcaption, footer, header, hgroup, menu, nav, output, ruby, section, summary, time, mark, audio, video { margin: 0; padding: 0; border: 0; }

ul { margin-top: 0; }

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure, footer, header, hgroup, menu, nav, section, main { display: block; }

blockquote, q { quotes: none; }

blockquote:before, blockquote:after, q:before, q:after { content: ''; content: none; }

table { border-collapse: collapse; border-width: 0px; padding: 0px; margin: 0px; }

html { font-size: 100%; font-size: 1rem; line-height: 1.6; -webkit-text-size-adjust: none; -ms-text-size-adjust: none; height: 100%; }

html.mm-opened { height: auto; }

body { color: #202020; margin: 0px; padding: 0px; font-family: HelveticaCustom, Arial, sans-serif; background-color: #fafafa; font-size: 18px; height: 100%; line-height: 1.6; position: relative; -webkit-font-smoothing: antialiased; -moz-osx-font-smoothing: grayscale; }

input, textarea { color: #333; font-family: HelveticaCustom, Arial, sans-serif; outline: none; border-radius: 0; -moz-border-radius: 0; -webkit-border-radius: 0; }

input[type="button"], input[type="submit"], button { cursor: pointer; }

td { margin: 0px; padding: 0px; }

form { padding: 0px; margin: 0px; }

a, input[type=submit], input[type=button], button { -webkit-appearance: none; outline: none !important; }

*:before, *:after { -webkit-box-sizing: border-box; -moz-box-sizing: border-box; box-sizing: border-box; }

.clearfix:after, .wrapper:after, .row:after { content: "."; display: block; height: 0; clear: both; visibility: hidden; }

a { color: #aaa; text-decoration: underline; outline: none; -webkit-tap-highlight-color: rgba(0, 0, 0, 0); }

a:visited, a:hover { text-decoration: none; color: #aaa; }

a:active, a:focus { outline: none; }

a:focus { text-decoration: none; }

a:hover { color: #aaa; }

a:active { background-color: transparent; }

textarea, input[type=url], input[type=search], input[type=number], input[type=password], input[type=tel], input[type=text], input[type=email] { height: 38px; border: 1px solid #aaa; background-color: #fff; margin: 0; font: 24px/38px HelveticaCustom, Arial, sans-serif; color: #aaa; padding: 0 15px; vertical-align: middle; display: inline-block; max-width: 100%; }

textarea.placeholder, input[type=url].placeholder, input[type=search].placeholder, input[type=number].placeholder, input[type=password].placeholder, input[type=tel].placeholder, input[type=text].placeholder, input[type=email].placeholder { color: #c7c7c7; }

