textarea { width: 222px; height: 102px; font: 16px/20px HelveticaCustom, Arial, sans-serif; overflow: auto; }

@keyframes pulsemy { from { box-shadow: rgba(244, 153, 29, 0.1) 0 0 4px 0, rgba(244, 153, 29, 0) 0 0 8px 35px; }
  to { box-shadow: rgba(244, 153, 29, 0) 0 0 8px 50px, rgba(244, 153, 29, 0.1) 0 0 4px 0; } }

@keyframes pulsemy-hover { from { box-shadow: rgba(0, 0, 0, 0.2) 0 0 4px 0, rgba(0, 0, 0, 0) 0 0 8px 35px; }
  to { box-shadow: rgba(0, 0, 0, 0) 0 0 8px 80px, rgba(0, 0, 0, 0.1) 0 0 4px 0; } }

@keyframes pulsemy-none { from { box-shadow: rgba(0, 0, 0, 0.2) 0 0 0 0, rgba(0, 0, 0, 0) 0 0 0 0; }
  to { box-shadow: rgba(0, 0, 0, 0) 0 0 0 0, rgba(0, 0, 0, 0.1) 0 0 0 0; } }

input:-webkit-autofill, input:-webkit-autofill:hover, input:-webkit-autofill:focus, input:-webkit-autofill:active { -webkit-box-shadow: 0 0 0 0 white inset !important; }

input:-webkit-autofill { -webkit-text-fill-color: #bdbdbd !important; }

.button, a.button, input[type=button], input[type=submit] { height: 60px; border: 1px solid #202020; background-color: #fff; font: 24px/58px HelveticaCustom, Arial, sans-serif; transition: .2s; margin: 0; padding: 0 52px; color: #202020; vertical-align: middle; display: inline-block; cursor: pointer; text-decoration: none; text-align: center; border-radius: 5px; max-width: 100%; }

.button:hover, a.button:hover, input[type=button]:hover, input[type=submit]:hover { background-color: #F4991D; border-color: #F4991D; color: #fff; text-decoration: none; }

::-webkit-input-placeholder { opacity: 1; }

:-ms-input-placeholder { opacity: 1; }

::-moz-placeholder { opacity: 1; }

:focus::-webkit-input-placeholder { color: transparent !important; }

:focus:-ms-input-placeholder { color: transparent !important; }

:focus::-moz-placeholder { color: transparent !important; }

textarea:invalid, input:invalid { box-shadow: none; }

.hidden { display: none; }

form { margin: 0; padding: 0; }

img { border-style: none; height: auto; max-width: 100%; }

.pull-left { float: left; margin: 0 11px 3px 0; }

.pull-right { float: right; margin-left: 9px; margin: 0 0 3px 11px; }

.alignleft { float: left; margin: 0 11px 3px 0; }

.alignright { float: right; margin: 0 0 3px 11px; }

.aligncenter { display: block; margin: 0 auto 22px; }

/* text position */
.text-left { text-align: left !important; }

.text-center { text-align: center !important; }

.text-right { text-align: right !important; }

.nowrap { white-space: nowrap !important; }

/* для photobox */
.pbWrapper > div { width: 1200px; height: 700px; max-width: 90%; max-height: 90%; }

.pbWrapper { max-width: 100%; }

